import React, {useState, Fragment} from 'react';
import Icon from '../Icon/Icon'
import {numberBrazilianMoney,IntegerNumberBrazilian} from '../../util/Utilities'
import {
    Tooltip
} from 'reactstrap';

const ListCotacao = (props) => {
    const [tooltipsin, setTooltipisin] = useState(false)
    const [tooltidata, setTooltipdata] = useState(false)
    const [tooltipfech, setTooltipfech] = useState(false)
    const [tooltipmax, setTooltipmax] = useState(false)
    const [tooltipmin, setTooltipmin] = useState(false)
    const [tooltipneg, setTooltipneg] = useState(false)
    const [tooltipvol, setTooltipvol] = useState(false)


    return (
        props.cotacao ?
        <ul className="list-group">
            <li className={`list-group-item active bg-${props.bgcolor} text-center`}>
                <span className="enfase-title">
                    <span className="font-number pr-2">
                        <Icon icon="chart-line"/>
                    </span>
                    Cotao
                </span>
            </li>
            <li className="list-group-item list-group-item-action"><span className="enfase">Cdigo: </span> {props.cotacao.codneg.cod_neg}</li>
            <li className="list-group-item list-group-item-action" id="cotisin"><span className="enfase">Cdigo ISIN: </span>{props.cotacao.codneg.cod_isin}</li>
            <li className="list-group-item list-group-item-action" id="cotdata"><span className="enfase">Data: </span>{props.cotacao.data_ult_cotacao}</li>
            <li className="list-group-item list-group-item-action" id="cotpfech"><span className="enfase">Preo: </span>{numberBrazilianMoney(props.cotacao.cotacao)}</li>
            <li className="list-group-item list-group-item-action" id="cotmax"><span className="enfase">Preo mximo (12 meses): </span>{numberBrazilianMoney(props.cotacao.preco_max_12_meses)}</li>
            <li className="list-group-item list-group-item-action" id="cotmin"><span className="enfase">Preo mnimo (12 meses): </span>{numberBrazilianMoney(props.cotacao.preco_min_12_meses)}</li>
            <li className="list-group-item list-group-item-action" id="cotneg"><span className="enfase">Quantidade mdia de negcios (12 meses): </span>{IntegerNumberBrazilian(props.cotacao.negocios_medio_12_meses)}</li>
            <li className="list-group-item list-group-item-action" id="cotvol"><span className="enfase">Volume mdio (12 meses): </span>{numberBrazilianMoney(props.cotacao.volume_medio_12_meses)}</li>
            <Fragment>
                <Tooltip className="tooltip-dark" placement='auto' isOpen={tooltipsin}
                        target="cotisin" toggle={() => setTooltipisin(prev => !prev)}>
                    Nmero de identificao internacional do ttulo financeiro.
                </Tooltip>
                <Tooltip className="tooltip-dark" placement='auto' isOpen={tooltidata}
                        target="cotdata" toggle={() => setTooltipdata(prev => !prev)}>
                    Data da cotao.
                </Tooltip>
                <Tooltip className="tooltip-dark" placement='auto' isOpen={tooltipfech}
                        target="cotpfech" toggle={() => setTooltipfech(prev => !prev)}>
                    Preo de fechamento.
                </Tooltip>
                <Tooltip className="tooltip-dark" placement='auto' isOpen={tooltipmax}
                        target="cotmax" toggle={() => setTooltipmax(prev => !prev)}>
                    Cotao mxima dos ltimos 12 meses
                </Tooltip>
                <Tooltip className="tooltip-dark" placement='auto' isOpen={tooltipmin}
                        target="cotmin" toggle={() => setTooltipmin(prev => !prev)}>
                    Cotao mnimo dos ltimos 12 meses
                </Tooltip>
                <Tooltip className="tooltip-dark" placement='auto' isOpen={tooltipneg}
                        target="cotneg" toggle={() => setTooltipneg(prev => !prev)}>
                    Quantidade mdia de negcios por dia durante os ltimos 12 meses
                </Tooltip>
                <Tooltip className="tooltip-dark" placement='auto' isOpen={tooltipvol}
                        target="cotvol" toggle={() => setTooltipvol(prev => !prev)}>
                    Volume mdio por dia durante os ltimos 12 meses
                </Tooltip>
            </Fragment>
        </ul>

        : null
    );
};

export default ListCotacao;